    export default {"info":{"title":"Арабский язык.\\n 1-я серия. От красоты к совершенству.","paramsArray":[["издатель: Аль-Джазира"]]},"content":{"001":{"title":"1.1.","type":"richMedia","content":{"phrases":{"100":{"text":"несмотря на то, что язык запутанный, необычный,"},"101":{"text":"язык следует законам ритма,"},"102":{"text":"просодии, музыки и размера,"},"103":{"text":"красоты образов, единства сюжета и множества целей."},"001":{"text":"В мире лошадей, верблюдов и пустынь было начало"},"002":{"text":"начало арабской речи."},"003":{"text":"Не было замкнутых пространств,"},"004":{"text":"зато продолжалось движение, смешивалось влияние и происходили переселения,"},"005":{"text":"затрудняющие изучение оттиска основы, оригинала,"},"006":{"text":"(с которого) множатся ответвления, а источник один."},"007":{"text":"Бесспорно, что рождение арабского языка из семитской семьи"},"008":{"text":"относится к сотням столетий до восхода солнца ислама."},"009":{"text":"(музыка)"},"010":{"text":"Учёные разошлись в определении родины первых семитов"},"011":{"text":"и не пришли к бесспорному мнению."},"012":{"text":"Вероятнее, она была в юго-западной части Аравийского полуострова,"},"013":{"text":"особенно в районе Хиджаза, Нежда и Йемена."},"014":{"text":"Мы говорим: арабский язык это часть"},"015":{"text":"семьи семитских языков, которая включает более 25 языков,"},"016":{"text":"и протянулась географически с Африканского рога:"},"017":{"text":"амхарский,"},"018":{"text":"геэз,"},"019":{"text":"и языки"},"020":{"text":"древне-эфиопские"},"021":{"text":"пока не достигает далёкого севера Ирака,"},"022":{"text":"это набатейский, арамейский, сирийский."},"023":{"text":"И арабские языки, на любой вкус и цвет,"},"024":{"text":"восходящие к одному из двух языков-источников."},"025":{"text":"Языку племён Аднан на севере арабского полуострова"},"026":{"text":"и языку племён Кахтан на юге."},"027":{"text":"За семь веков до ислама арабы были племенами,"},"028":{"text":"различными народами и племенами,"},"029":{"text":"каждая область говорила на диалекте ."},"030":{"text":"Эти диалекты, например, у жителей юга, потомков Кахтана в Йемене и вокруг него,"},"031":{"text":"жителей севера, потомков Аднана."},"032":{"text":"Все говорили на своём диалекте с соседями: племена Азд, Тайиб, Химьяр."},"033":{"text":"Как сказал Абу Амр Аль Аля,"},"034":{"text":"когда хотел рассказать о различии между севером и югом в языке,"},"035":{"text":"он сказал: Язык Химьяр не такой как наш язык,"},"036":{"text":"их арабский не как наш арабский."},"037":{"text":"У древнего арабского языка было"},"038":{"text":"много функциональных уровней:"},"039":{"text":"общий язык,"},"040":{"text":"язык поэзии, публичных речей, диалогов и хвалебных речей,"},"041":{"text":"и язык племён,"},"042":{"text":"который отклонялся от нормы по мере удаления от общего языка."},"043":{"text":"Было нормальным, что северный язык доминировал над южным,"},"044":{"text":"а язык (племени) Курейш над остальными языками"},"045":{"text":"из-за его религиозной, экономической и общественной значимости."},"046":{"text":"В пользу Курейша"},"047":{"text":"было его главенство в хадже."},"048":{"text":"А суть хаджа величайшее религиозное мероприятие,"},"049":{"text":"праздник торговли,"},"050":{"text":"и собрание,"},"051":{"text":"на котором собирались арабы и обменивались мнениями,"},"052":{"text":"старались сблизиться друг с другом на языке взаимопонимания,"},"053":{"text":"на объединяющей основе языка народа, где они находились."},"054":{"text":"Несмотря на то, что у них различались некоторые слова,"},"055":{"text":"большинство арабов знали"},"056":{"text":"этот диалект, потому что они контактировали"},"057":{"text":"ежегодно с"},"058":{"text":"этим племенем, даже до ислама."},"059":{"text":"Поэтому арабы на ярмарке хаджа"},"060":{"text":"и соседних ярмарках, сближались друг с другом на базе"},"061":{"text":"речи Курейш, на основе диалекта Курейш, на основе"},"062":{"text":"стандартов Курейш в повествовании (синтаксис) и звуковом исполнении (фонетика)."},"063":{"text":"Арабы устраивали ярмарки для торговли и обмена,"},"064":{"text":"а их социальная природа призывала"},"065":{"text":"к сближению в речи,"},"066":{"text":"переговорах о мнениях,"},"067":{"text":"оригинальности в стихах"},"068":{"text":"и соревнованию в красноречии"},"069":{"text":"Ярмарка Указа была"},"070":{"text":"красноречивым подтверждением почитания арабами красоты языка."},"071":{"text":"Доисламский период считается"},"072":{"text":"важным звеном в истории литературного арабского,"},"073":{"text":"который достиг непревзойденного уровня развития."},"074":{"text":"Арабский язык"},"075":{"text":"в доисламский период"},"076":{"text":"над ним преобладал характер"},"077":{"text":"языка, возникшего в среде пустыни."},"078":{"text":"Отсюда простота и материальность."},"079":{"text":"Поэтому у арабов преобладали"},"080":{"text":"имена естественной окружающей среды, окружавшей их, они говорили о разновидностях"},"081":{"text":"гор, дюн, песка."},"082":{"text":"Так язык стал материальным, скрывая слова, вроде: послание, судный день,"},"083":{"text":"сокровенное и весь абстрактный мир."},"084":{"text":"Богослужение было материальное,"},"085":{"text":"убеждения идолопоклонские, мышление,"},"086":{"text":"философия не развивались, так как требовался язык нового уровня."},"087":{"text":"Однако мир чувств, напротив, был богат."},"088":{"text":"Бедуин (кочевник) это человек первобытный, спонтанный, сильный от природы."},"089":{"text":"Поймало моё сердце печаль. Девушка"},"090":{"text":"С длинными ресницами и чёрными глазами,"},"091":{"text":"молодая, фигуристая, стройная, соблазнительная,"},"092":{"text":"стыдливая, великолепная, прекрасная как луна."},"093":{"text":"Наследие, которое принесла эпоха невежества для нас и истории"},"094":{"text":"до нас огромное."},"095":{"text":"В нём стихи, пословицы, притчи, речи и послания."},"096":{"text":"Эпоха невежества является одной из стадий языкового развития доарабского языка,"},"097":{"text":"того языка, который унаследовал эту стадию и стал языком зрелым, красивым, ёмким, волнующим."},"098":{"text":"Во-вторых, есть плавность в выражениях, простота, сила, ясность, эстетика."},"099":{"text":"Есть и кое-что удивительное в этом периоде поэзии:"},"000":{"start":0,"end":0,"text":""}}}},"002":{"title":"1.2.","type":"richMedia","content":{"phrases":{"001":{"text":"Пришел Амр Аль-Кайс!"},"002":{"text":"Пришел Амр Аль-Кайс!"},"003":{"text":"Самым возвышенным,"},"004":{"text":"что создали древние посредством языка"},"005":{"text":"была поэзия."},"006":{"text":"А самым возвышенным в поэзии"},"007":{"text":"была (поэма) Муаллакат,"},"008":{"text":"прикрепленная к покрывалу Каабы"},"009":{"text":"из уважения к ней и признания"},"010":{"text":"за её эстетику."},"011":{"text":"Я крепость, построенная для народа Абс,"},"012":{"text":"когда строят герои крепость."},"013":{"text":"Я сделан из сердца скалы."},"014":{"text":"Когда рассыпятся горы,"},"015":{"text":"я не рассыплюсь."},"016":{"text":"Поэты (эпохи) невежества, вроде Амра Аль-Кайса, Антара (ибн Шаддада) и другие"},"017":{"text":"Набига Аз-Зубйани"},"018":{"text":"читали свои стихи, но не знали размера,"},"019":{"text":"стоп, стихотворного размера, о которых говорил Халил."},"020":{"text":"Поэты читали свои стихи"},"021":{"text":"и улучшали их, выбирая чистый язык,"},"022":{"text":"возвышенный язык, который понимали все арабы"},"023":{"text":"и не выбирали слова чужие, заимствованные."},"024":{"text":"Подтверждение (этому), что поэты создавали так называемые ежегодники."},"025":{"text":"Что такое ежегодники ? Это единая касыда, в которой располагался весь (творческий) год. Такое изобретение"},"026":{"text":"они писали её четыре месяца,"},"027":{"text":"потом редактировали и исправляли четыре месяца,"},"028":{"text":"потом демонстрировали критикам четыре месяца."},"029":{"text":"И если критики одобряли, то представляли людям."},"030":{"text":"Ты видел навоз (белых) газелей во дворах и на равнинах,"},"031":{"text":"словно это семена перца,"},"032":{"text":"как будто я накануне разлуки в ответственный день"},"033":{"text":"???"},"034":{"text":"???"},"035":{"text":"???"},"036":{"text":"???"},"037":{"text":"???"},"038":{"text":"???"},"039":{"text":"???"},"040":{"text":"???"},"041":{"text":"???"},"042":{"text":"???"},"043":{"text":"Были критики, оценивающие эту поэзию"},"044":{"text":"как замечательную, достойную и т.д."},"045":{"text":"Начало арабской критики было на этих ярмарках в"},"046":{"text":"периодическом оценивании поэтов и поэтесс."},"047":{"text":"Пусть даже люди соревновались,"},"048":{"text":"к ним прислушивался какой-нибудь критик, чтоб они поддерживали язык"},"049":{"text":"мутакариб (стиль стиха). Язык поэтов был"},"050":{"text":"языком высоким, возвышенным"},"051":{"text":"и во многом"},"052":{"text":"искусством с точки зрения техники, и"},"053":{"text":"красноречия и подобен общему языку"},"054":{"text":"для арабов севера Аравийского полуострова."},"055":{"text":"К скале придут вожди,"},"056":{"text":"словно она знамя,"},"057":{"text":"на вершине которой огонь."},"058":{"text":"Если бы Абу Басыр не читал мне стихи до тебя"},"059":{"text":"я бы сказал, что ты самый поэтический человек."},"060":{"text":"Я, клянусь Аллахом, лучший поэт, чем ты и она."},"061":{"text":"Пока ты говоришь что?"},"062":{"text":"Наши благородные веки блестят по утру,"},"063":{"text":"а мечи истекают кровью храбрецов"},"064":{"text":"???"},"065":{"text":"???"},"066":{"text":"???"},"067":{"text":"???"},"068":{"text":"???"},"069":{"text":"???"},"070":{"text":"???"},"071":{"text":"???"},"072":{"text":"Несомненно,"},"073":{"text":"у арабов (эпохи) невежества"},"074":{"text":"было знание о письменности"},"075":{"text":"особенно в южных поселениях,"},"076":{"text":"где найдены камни, скалы и другие предметы,"},"077":{"text":"использованные для записи."},"078":{"text":"Тогда арабская письменность была"},"079":{"text":"без точек,"},"080":{"text":"без огласовок."},"081":{"text":"Его основой было набатейское письмо,"},"082":{"text":"от которого оно произошло."},"083":{"text":"Арабская письменность до ислама"},"084":{"text":"ограничивалась (написанием) договоров и документов"},"085":{"text":"До"},"086":{"text":"ниспослания благородного Корана нашему господину, Мухаммаду, да благословит его Аллах и приветствует, арабская вязь"},"087":{"text":"не называлась вязью,"},"088":{"text":"она была письменностью"},"089":{"text":"и была записками"},"090":{"text":"тяжёлыми для записи."},"091":{"text":"Арабы восхищались и почитали умеющих писать, даже если сами не умели"},"092":{"text":"и арабы называли письменность клеймом, потому что они видели, что пишущий высекает, подобно клейму на одежде"},"093":{"text":"или клеймления денег."},"094":{"text":"Всё это признаки пережитков устного мышления,"},"095":{"text":"устной культуры, устного склада, устного языка."},"000":{"start":0,"end":0,"text":""}}}},"003":{"title":"1.3.","type":"richMedia","content":{"phrases":{"100":{"text":"украшении, которое бы соответствовало ей и записи этого свитка."},"001":{"text":"Арабский язык оставался в таком положении"},"002":{"text":"до заката эпохи невежества,"},"003":{"text":"когда язычество и невежество сменилось эпохой ислама,"},"004":{"text":"с закреплением арабского"},"005":{"text":"как эмблемы для него и его последователей."},"006":{"text":"Первое божественное указание, исходящее от Всевышнего Аллаха это Читай"},"007":{"text":"мы рассматриваем это как первый закон, предписывающий нам читать на арабском языке"},"008":{"text":"11 аятов,"},"009":{"text":"все пришли как текст и предписание,"},"010":{"text":"потому что Коран"},"011":{"text":"снизошел, ниспослан, установлен и внушён"},"012":{"text":"на ясном арабском языке."},"013":{"text":"Здесь божественный замысел, в этом выборе,"},"014":{"text":"скрывающийся в его правильности,"},"015":{"text":"в его совершенстве, что это вселенский язык."},"016":{"text":"Я прибегаю к защите Аллаха от шайтана побиваемого."},"017":{"text":"С именем Аллаха, милостивого, милосердного"},"018":{"text":"Алиф, Лам, Ра (названия букв)"},"019":{"text":"Это — аяты ясного Писания."},"020":{"text":"Воистину, Мы ниспослали его в виде Корана на арабском языке, чтобы вы могли понять его."},"021":{"text":"Появление ислама и ниспослание"},"022":{"text":"его мудрого писания на ясном арабском языке"},"023":{"text":"создало широкий качественный переход"},"024":{"text":"в развитии арабского языка"},"025":{"text":"и изменило положение арабского языка,"},"026":{"text":"превратив его из локального в мировой."},"027":{"text":"Он двигался и двигался"},"028":{"text":"от границ к широкому распространению."},"029":{"text":"Коран, понятное дело,"},"030":{"text":"не только руководство для арабов:"},"031":{"text":"О, люди он обращается к человечеству,"},"032":{"text":"он рассказывает о существах и созданиях,"},"033":{"text":"говорит о разных небесных телах"},"034":{"text":"это означает, что Всевышний Аллах выделяет этот язык, обращаясь на нём ко всем своим созданиям."},"035":{"text":"Коран послужил арабскому укреплением,"},"036":{"text":"послужил арабскому записью, послужил арабскому распространением,"},"037":{"text":"и послужил арабскому укреплением"},"038":{"text":"его уровня в подаче, возвысил художественно и эстетически,"},"039":{"text":"достоинством и подачей."},"040":{"text":"А ещё глубже и сильнее, Коран послужил"},"041":{"text":"арабскому языку"},"042":{"text":"в сфере"},"043":{"text":"передачи"},"044":{"text":"знания"},"045":{"text":"и истин,"},"046":{"text":"связанных с рассказами о пророках, истории земли, истории вселенной,"},"047":{"text":"с начала времен,"},"048":{"text":"о нашем господине Адаме, и о настоящих убеждениях, нравах и ценностях."},"049":{"text":"Вот Йусуф (Иосиф) сказал своему отцу: «О мой отец! Я видел одиннадцать звезд, солнце и луну."},"050":{"text":"Я видел, как они поклонились мне»."},"051":{"text":"Было необходимо, чтобы для этого было влияние"},"052":{"text":"на устройство языка, то есть на структуру"},"053":{"text":"языка в общем"},"054":{"text":"в выражении тем, о которых рассказывают языки"},"055":{"text":"каждого народа."},"056":{"text":"Коран выстроил глубокую терминологию,"},"057":{"text":"ставшую полезной для всего арабского языка."},"058":{"text":"Никто сегодня не сможет сказать, что кораническая терминология"},"059":{"text":"монополизирована Кораном, и не входит в арабский."},"060":{"text":"Коран создал общеупотребительный словарный корпус."},"061":{"text":"Это как минимум 4 тысячи глубоких коранических терминов,"},"062":{"text":"несущих смыслы (связанные с) убеждениями, духовностью, нравственностью."},"063":{"text":"Язык хотим мы или нет это сосуд для мышления"},"064":{"text":"в разных его проявлениях:"},"065":{"text":"понятия, взгляды, знания, ощущения и чувства."},"066":{"text":"Коран оставил своим удивительным языком и стилем,"},"067":{"text":"явный след на мышлении, нраве и религии,"},"068":{"text":"рассеял мрак невежества."},"069":{"text":"И символом мусульман стало:"},"070":{"text":"Нун, клянусь письменной тростью и тем, что пишут."},"071":{"text":"Письменная трость закрепилась в исламской культуре как инструмент для записи"},"072":{"text":"и средство для заучивания Корана и его сохранения."},"073":{"text":"???"},"074":{"text":"Когда начал ниспосылаться Коран,"},"075":{"text":"окружили посланника Аллаха 40 свободных писцов,"},"076":{"text":"если ниспосылалось откровение, они спешили записывать."},"077":{"text":"И было у посланника Аллаха 60 писарей для откровения,"},"078":{"text":"других, не свободных (занятых)."},"079":{"text":"Если ниспосылалась сура или некоторые хадисы, им вменялось внимательно"},"080":{"text":"приписать это в таком-то месте, к такой-то суре."},"081":{"text":"Для книги Всевышнего Аллаха совмещалось запоминание наизусть и письменная запись"},"082":{"text":"Закончил ли ты считать аяты суры Пещера?"},"083":{"text":"Да."},"084":{"text":"Сколько их? 110."},"085":{"text":"Маленькое общество они не записывали десятков, но с Кораном стала осуществимой сотня."},"086":{"text":"При записи откровения произошел переход от общества устного к письменному,"},"087":{"text":"от невежественного общества к обществу письма и Корана."},"088":{"text":"Коран вывел арабский язык на письменный уровень."},"089":{"text":"Когда мы говорим письменный, имеем ввиду накапливаемость,"},"090":{"text":"развитие, запоминание, потому что то, что не записывается теряется,"},"091":{"text":"когда люди начали записывать, произошло накопление"},"092":{"text":"и цивилизация стала равняться на наследие,"},"093":{"text":"потому что, когда ты записываешь, ты сохраняешь."},"094":{"text":"Арабский не мог сохраняться, кроме как письменной фиксацией, а письменная фиксация стала необходима благодаря Корану."},"095":{"text":"Было необходимо"},"096":{"text":"существование шрифта и искусства, соответствующих самой великой книге, ниспосланной посланнику Аллаха, мир ему и благословение,"},"097":{"text":"поэтому соревновались каллиграфы"},"098":{"text":"в улучшении этого искусства"},"099":{"text":"и его обогащении,"},"000":{"start":0,"end":0,"text":""}}}},"004":{"title":"1.4.","type":"richMedia","content":{"phrases":{"001":{"text":"Я закончил от последней 1/8 до первой 1/4 суры Аль-Бакара."},"002":{"text":"Начинай со второй четверти"},"003":{"text":"и не останавливайся, пока не закончишь первый хизб от всей суры."},"004":{"text":"Наш язык, арабский,"},"005":{"text":"в начале"},"006":{"text":"не имел точек (для согласных) и огласовок."},"007":{"text":"Точки согласных, вы знаете, насколько они важны"},"008":{"text":"для того, чтобы различать буквы."},"009":{"text":"Что касается огласовок,"},"010":{"text":"они появились позже"},"011":{"text":"и возможно это внесло вклад"},"012":{"text":"и привело к распространению речи,"},"013":{"text":"когда мусульмане начали распространять"},"014":{"text":"религию Всевышнего Аллаха"},"015":{"text":"в областях полуострова, в Персии, Леванте"},"016":{"text":"и других местах и стали смешиваться с"},"017":{"text":"мусульманами новые (люди) с другими языками."},"018":{"text":"В частности, так как Коран не имел"},"019":{"text":"точек и огласовок и начали"},"020":{"text":"появляться ошибки у новых мусульман. Так что Хузейфа"},"021":{"text":"Ибн Аль-Йаман, да будет доволен им Аллах, пришел"},"022":{"text":"за помощью к повелителю правоверных Усману и сказал: спаси исламскую умму, прежде"},"023":{"text":"чем она разделится в вопросе книги Аллаха."},"024":{"text":"???"},"025":{"text":"А вот рассказ о кочевнике."},"026":{"text":"Он принял ислам"},"027":{"text":"и устремился к учёбе,"},"028":{"text":"к заучиванию Корана,"},"029":{"text":"и отправился в Медину."},"030":{"text":"И в рассказе приводится,"},"031":{"text":"что ему читал"},"032":{"text":"один человек"},"033":{"text":"слова Всевышнего:"},"034":{"text":"Предупреждение от Аллаха и Его посланника к людям в день великого паломничества:"},"035":{"text":"Аллах отрекается от многобожников и Его посланника."},"036":{"text":"???"},"037":{"text":"???"},"038":{"text":"Если Аллах отрекается от своего посланника,"},"039":{"text":"то я отрекаюсь от тебя."},"040":{"text":"Нарушение правил,"},"041":{"text":"этот сбой изменил смысл"},"042":{"text":"и сделал кочевника или почти сделал отступником"},"043":{"text":"от ислама. Он понял (не тот) смысл и чуть не отступился от ислама."},"044":{"text":"Халиф Умар Ибн Аль-Хаттаб,"},"045":{"text":"когда дошла до его ушей эта новость,"},"046":{"text":"приказал"},"047":{"text":"читать Коран людям,"},"048":{"text":"только знатокам арабского языка."},"049":{"text":"Когда мусульмане заметили серьёзную нехватку, начали думать о"},"050":{"text":"защите арабского от ошибок, Корана от искажений, свитков от описок."},"051":{"text":"И всегда вдохновением был Коран, с которым был основательно связан арабский язык."},"052":{"text":"Первым обратившим внимание был Абу Асвад Ад-Дувалий,"},"053":{"text":"сподвижник нашего господина Али,"},"054":{"text":"когда его дочь говорила с ошибками перед ним."},"055":{"text":"Он не был занят сражениями и завоеваниями, а она воспитывалась мамой неарабкой"},"056":{"text":"и научилась от неё ошибкам в языке. Он пришел с ней"},"057":{"text":"на собрание Али Ибн Абу Талиба"},"058":{"text":"и сказал: Пропал арабский язык и пропал Коран, о повелитель правоверных."},"059":{"text":"И ответил ему наш господин Али: Поистине речь это имя, действие и частица,"},"060":{"text":"так следуй этим направлением, о Абу Асвад!"},"061":{"text":"То есть он начал составление этих правил и"},"062":{"text":"название грамматика произошло от слова нашего господина Али"},"063":{"text":"следуй этим направлением, о Абу Асвад"},"064":{"text":"и продиктовал своему писцу:"},"065":{"text":"Если ты видишь как я вытягиваю (адумму) губы при (произнесении) буквы,"},"066":{"text":"ставь точку сбоку от неё,"},"067":{"text":"а если ты видишь, что я открываю (афтаху) губы при букве,"},"068":{"text":"ставь точку над ней,"},"069":{"text":"а если видишь, что я сгибаю (аксиру) губы при букве,"},"070":{"text":"ставь точку снизу,"},"071":{"text":"а если я делаю вот так,"},"072":{"text":"через нос, ставь две точки."},"073":{"text":"С именем Аллаха, милостивого, милосердного"},"074":{"text":"когда пришел."},"075":{"text":"Эта система точек, которая развилась далее"},"076":{"text":"в то, что известно под названием движения флексии"},"077":{"text":"или огласовки."},"078":{"text":"Историки единогласны, что Абу Асвад Ад-Дувалий"},"079":{"text":"сделал важный поворот в истории арабского языка"},"080":{"text":"и ознаменовал новый век взаимоотношений с его нормами и правилами."},"081":{"text":"Омеййады много трудились, чтобы распространять вместе с исламом и арабский язык."},"082":{"text":"Разумеется, с ограниченными средствами. Не было в те времена школ,"},"083":{"text":"мечети были школами."},"084":{"text":"Омеййады заботились об арабском языке, потому что они были арабами,"},"085":{"text":"их вкус арабский, склад ума арабский, религия арабская, темперамент арабский."},"086":{"text":"И их забота об арабском была от их арабской натуры, как сказал Джахиз:"},"087":{"text":"от их религиозности и заботы о Коране."},"088":{"text":"Как мы уже сказали, в их время, при содействии их правителей"},"089":{"text":"завершили огласовывание текста."},"090":{"text":"Их заслуга также введение шрифта с диакритическими точками."},"091":{"text":"Их другая заслуга, очень важная, что они ввели арабский язык в гос.учреждениях."},"092":{"text":"В 87 году хиджры Халиф издал смелый указ об арабизации гос.структур."},"093":{"text":"Арабизация гос.учреждений потребовала и арабизацию монет."},"094":{"text":"Появился первый арабский динар с надписями на арабском: некоторых аятов,"},"095":{"text":"имен и знаков."},"000":{"start":0,"end":0,"text":""}}}},"005":{"title":"1.5.","type":"richMedia","content":{"phrases":{"001":{"text":"Если бы развалины могли говорить,"},"002":{"text":"то поведали бы о загадке распространения арабского языка в Андалусии"},"003":{"text":"как местные жители встретили его,"},"004":{"text":"что церковнослужители были вынуждены переводить молитвы на арабский,"},"005":{"text":"чтобы христиане понимали их."},"006":{"text":"Арабский язык распространился по Андалусии в течении 50 лет, половины века."},"007":{"text":"Это феноменальный срок в мировых масштабах. Как он распространился?"},"008":{"text":"По причине политического указа, изданного халифом Абд Ар-Рахманом Аль-Авсатом,"},"009":{"text":"велевшим людям говорить на арабском языке."},"010":{"text":"Дело в том, что переживали коренные жители"},"011":{"text":"(вандалы) притеснение и насилие со стороны колонизировавших их коптов"},"012":{"text":"и из-за этого они принимали ислам и арабский язык добровольно и увлечённо."},"013":{"text":"И в добавок к этому достоинство арабов, которых отправили Омеййады на завоевания, до их падения (их династии)."},"014":{"text":"Они отправляли арабские племена из группы Кайс и Кахтан (йеменцев),"},"015":{"text":"среди которых были поэты, проповедники и писатели,"},"016":{"text":"и все они настоящие арабы."},"017":{"text":"Они образовали учёный класс в Андалусии."},"018":{"text":"Этот класс (слой) был большим по количеству"},"019":{"text":"и сильным по качеству."},"020":{"text":"Европейцы, которые хотели специализироваться в медицине или инженерии начали"},"021":{"text":"отправлять своих студентов в Андалусию и в столицу Кордову."},"022":{"text":"И первым, что им приходилось выучить, был арабский язык."},"023":{"text":"Кто читал рассказы о монахах, священниках и востоковедах,"},"024":{"text":"(заметил) как они мастерски владели арабским языком"},"025":{"text":"и владели Кораном и хадисами лучше, чем некоторые арабы."},"026":{"text":"Христианским институтам вменялось в обязанность"},"027":{"text":"обучать своих учеников арабскому языку и в совершенстве владеть им."},"028":{"text":"???"},"029":{"text":"???"},"030":{"text":"???"},"031":{"text":"Мы подверглись воздействию других народов, как и они нашему,"},"032":{"text":"мы брали от них также, как они брали от нас."},"033":{"text":"Забота об арабском и языковое сочинительство"},"034":{"text":"появились в век Аббасидов,"},"035":{"text":"потому что в этом веке начали"},"036":{"text":"появляться языковые проблемы, так скажем,"},"037":{"text":"по причине многочисленности неарабов."},"038":{"text":"Потому что гос.учреждения стали управляться неарабами без присмотра за ними"},"039":{"text":"на уровне языковой практики и оказались в положении,"},"040":{"text":"когда нужно было принять меры по исправлению этого."},"041":{"text":"Перед нами арабские аббасидские поэты, великие поэты,"},"042":{"text":"мы видим у них говор или что называется диалектными словами,"},"043":{"text":"или простонародными и т.п."},"044":{"text":"В поэзии эпохи Аббасидов встречаются заимствованные, неарабские слова у великих поэтов"},"045":{"text":"В то же время в аббасидской поэзии встречаются слова"},"046":{"text":"из древнего вычурного арабского языка, неестественные и непонятные"},"047":{"text":"даже арабам."},"048":{"text":"С этого вызова началась борьба учёных."},"049":{"text":"И забота аббасидского государства о них (ученых) не могла игнорировать это."},"050":{"text":"Академическая борьба за сохранение арабского языка. Они начали систематизацию."},"051":{"text":"И систематизация переносит вас от спонтанного понимания языка к сознательному пониманию"},"052":{"text":"от стихийного понимания языка"},"053":{"text":"к практике направленного изучения языка, подобного изучению математики, через правила."},"054":{"text":"Учёные были вынуждены"},"055":{"text":"придумать правила, регулирующие этот язык."},"056":{"text":"И если кто-то выходит за рамки этих правил, тот выходит (из норм) языка."},"057":{"text":"Для этого установили правила так,"},"058":{"text":"как не устанавливали у арабов до ислама."},"059":{"text":"Установление всех формальностей произошло в эпоху Аббасидов."},"060":{"text":"Установление всех правил произошло в эпоху Аббасидов."},"061":{"text":"Великие языковеды, как Халил Ибн Ахмад Аль-Фарахиди, Сибавайх,"},"062":{"text":"его ученик Абу Али Ар-Румани и последующие языковеды были"},"063":{"text":"на протяжении эпохи Аббасидов при дворах их правителей и королей."},"064":{"text":"Эти правила, над которыми постарались древние (ученые)"},"065":{"text":"в их недрах, или как мы говорили ранее, в основах,"},"066":{"text":"впереди чудесный благородный Коран."},"067":{"text":"Второй источник"},"068":{"text":"хадисы господина посланников, избранника, да благословит его Аллах и приветствует."},"069":{"text":"Третий источник поэзия."},"070":{"text":"Поэзия, как вы знаете"},"071":{"text":"это стихи кочевых арабов."},"072":{"text":"Четвертый источник арабы:"},"073":{"text":"настоящие, благородные,"},"074":{"text":"которые говорили на языке с детства,"},"075":{"text":"от природы и интуитивно разбирались в этом."},"076":{"text":"Во-первых, была строгая карта"},"077":{"text":"племен, до которых не могли добраться."},"078":{"text":"Потому что племена окраин, всё же соприкасались и переплетались,"},"079":{"text":"хотя и не селились в городах."},"080":{"text":"Они (учёные) взяли сердце арабского полуострова,"},"081":{"text":"в закрытой пустыне и сохранили то, что было у них."},"082":{"text":"Было установлено это географическое условие сердце закрытой арабской пустыни, и установлено временное условие"},"083":{"text":"заимствовалось из литературного языка до конца 2-го века в городах"},"084":{"text":"и до конца 4-го века у бедуинов,"},"085":{"text":"потому что после этого (язык) был ненадежным."},"000":{"start":0,"end":0,"text":""}}}},"006":{"title":"1.6.","type":"richMedia","content":{"phrases":{"001":{"text":"Когда появились могущественные империи, вроде Аббасидской,"},"002":{"text":"они соревновались в привлечении каллиграфов,"},"003":{"text":"устраивали соревнования по написанию Священного Писания и государственных документов и т.п."},"004":{"text":"Благодаря этому созрели многие каллиграфы. Упомяну каллиграфа Ибн Мукла"},"005":{"text":"и каллиграфа Якута Мустасима. Имя каллиграфа отсылало"},"006":{"text":"к периоду правления султана или правителя."},"007":{"text":"В эту эпоху"},"008":{"text":"объединились культурные, религиозные и политические факторы"},"009":{"text":"для реализации важного прорыва в исламском мире."},"010":{"text":"Возник ранний интерес к мирским наукам"},"011":{"text":"и образованию новых знаний."},"012":{"text":"Исламский мир был в расцвете своей цивилизации"},"013":{"text":"и пользовался тем, что цивилизации взаимодействовали между собой"},"014":{"text":"и получали от этого взаимодействия важное для себя."},"015":{"text":"Всё это прибавляло арабскому языку цену,"},"016":{"text":"а арабской умме энергию."},"017":{"text":"Он был языком первого мира, в то время как мы были первым миром."},"018":{"text":"В то время наш язык был языком политики, экономики, торговли, науки, знаний, астрономии,"},"019":{"text":"а также создания и распространения переводов."},"020":{"text":"Руками арабов, которые объединили цивилизации,"},"021":{"text":"то есть связали цивилизации между собой."},"022":{"text":"Движение переводов началось как погоня, как страсть,"},"023":{"text":"потому что исламская религия религия развития и она призывает людей созерцать, извлекать уроки и искать знания, пусть даже в Китае."},"024":{"text":"Эта страсть привела мусульман в то время к желанию переводить."},"025":{"text":"Когда пришла эпоха Аббасидов"},"026":{"text":"и логика вещей, которая была накоплена и достигнута в цивилизацию Аббасидов,"},"027":{"text":"достигнутая официальными решениями, высшими указами"},"028":{"text":"и общественным сознанием, привели к этим переводам."},"029":{"text":"Движение по переводу и переписыванию,"},"030":{"text":"Дом мудрости,"},"031":{"text":"у них была основная роль"},"032":{"text":"в объединении"},"033":{"text":"и развитии арабского мира"},"034":{"text":"и арабского языка для того, чтобы культуры"},"035":{"text":"и науки иностранцев были заимствованы"},"036":{"text":"Приобретения цивилизации и достижения,"},"037":{"text":"которые были в медицине, инженерии,"},"038":{"text":"астрономии и других науках"},"039":{"text":"переведены с греческого языка"},"040":{"text":"на арабский язык."},"041":{"text":"Поговорим о службе, которую сослужили переводы для уммы."},"042":{"text":"Привнесение знаний и наук как это отразилось на арабском?"},"043":{"text":"Арабский язык включил терминологию этих наук."},"044":{"text":"Некоторые были неправильными"},"045":{"text":"(с точки зрения) идеального арабского, войдя в него извне,"},"046":{"text":"но необходимыми, как слово философия"},"047":{"text":"или слово география,"},"048":{"text":"или слово астролябия. Мы не знали этих слов."},"049":{"text":"Преобладали термины из астрономии, математики, философии, географии,"},"050":{"text":"геологии и многих наук, которые арабы не знали."},"051":{"text":"Мы взяли эти слова так, как они звучали, и арабизировали их."},"052":{"text":"Они вошли в тело языка и стали частью языка."},"053":{"text":"Не проблема, что мы воспользовались другими культурами,"},"054":{"text":"взяли от них, перевели их"},"055":{"text":"на наш арабский язык точно"},"056":{"text":"не искажая смысла"},"057":{"text":"языка оригинала."},"058":{"text":"И это один из способов развития арабского языка."},"059":{"text":"Иначе перевод был бы плохим, недостоверным."},"060":{"text":"Тексты получались бы плохими"},"061":{"text":"Но эти тексты не соотносятся с основным языком, потому что они не касаются его правил."},"062":{"text":"и не становятся (враждебной) колонной в цельном языке."},"063":{"text":"Наоборот, мы развились для словаря"},"064":{"text":"богатого, с науками и знаниями,"},"065":{"text":"и он стал языком, на который делался перевод."},"066":{"text":"Это знание, и это приобретение становится хранителем"},"067":{"text":"и продолжает переваривание, производство и экспорт этого"},"068":{"text":"знания и придает ему (языку) дополнительную ценность."},"069":{"text":"Но у всего есть свой предел."},"070":{"text":"И после падения Гранады и Багдада"},"071":{"text":"с арабским оплотом случилась болезнь"},"072":{"text":"и потеряли арабы своё место в цивилизации"},"073":{"text":"и проникла слабость в язык,"},"074":{"text":"который был сосудом мысли и знаний,"},"075":{"text":"символом развития."},"000":{"start":0,"end":0,"text":""}}}},"007":{"title":"1.7.","type":"richMedia","content":{"phrases":{"001":{"text":"Потом пришла эпоха Османов"},"002":{"text":"и отношения с арабским языком стали запутанными"},"003":{"text":"и неясными."},"004":{"text":"С приходом ислама арабский язык пришел естественным путем в этот регион."},"005":{"text":"После этого, известно, что разные государства сменяли (друг друга)"},"006":{"text":"на этих территориях и пришли сельджуки"},"007":{"text":"и, как известно, победили в сражении"},"008":{"text":"при Манцикерте в 1070 по хиджре"},"009":{"text":"ой, то есть от Рождества (Христова)."},"010":{"text":"После этого сельджуки выбрали"},"011":{"text":"арабские буквы как инструмент и способ передачи"},"012":{"text":"их литературы, знаний, культуры,"},"013":{"text":"официальных посланий и т.д."},"014":{"text":"Турки, когда приняли ислам, оставили свой язык"},"015":{"text":"и перестали писать на уйгурском"},"016":{"text":"и на готтурк."},"017":{"text":"Они оставили их и начали писать арабскими буквами."},"018":{"text":"Это указывает на их любовь к арабскому языку."},"019":{"text":"Арабские буквы, на самом деле, использовались в Турции более 1000 лет назад."},"020":{"text":"Арабский язык был в Османской империи одним из важнейших."},"021":{"text":"Известно, что арабский был языком науки и знаний в Османской Империи, в то время как"},"022":{"text":"турецкий был языком государства и учреждений."},"023":{"text":"Они любили арабский и почитали как язык Корана."},"024":{"text":"Отсюда их страсть к арабским буквам и переход на них."},"025":{"text":"Величайшими каллиграфами были турки"},"026":{"text":"и лучшие письменные османские росписи (сделали) турки."},"027":{"text":"Османская империя в свои золотые века поддерживала это искусство"},"028":{"text":"и оно стало одним из компонентов, символизирующих это государство."},"029":{"text":"Каллиграфы стали пользоваться всесторонним покровительством"},"030":{"text":"и пользовались уважением."},"031":{"text":"Для этого были прочные основы в эпоху Османского халифата. Султан должен был знать каллиграфию"},"032":{"text":"и положение каллиграфа было подобно положению министра."},"033":{"text":"Один султан даже держал чернильницу"},"034":{"text":"для известного каллиграфа Хафиза Османа"},"035":{"text":"и говорил: Я не представляю, что появится каллиграф вроде тебя"},"036":{"text":"или что появлялся подобный тебе до тебя или (появится) после тебя."},"037":{"text":"И сказал ему Хафиз Осман: Если будут султаны вроде тебя, держащие"},"038":{"text":"чернильницу для своего мастера, появятся тысячи Хафизов Османов."},"039":{"text":"Османское государство"},"040":{"text":"получало сильный и положительный эффект от привлечения известных каллиграфов."},"041":{"text":"Их приглашали во дворец султана"},"042":{"text":"и дарили им подарки."},"043":{"text":"Это служило целям Османского государства,"},"044":{"text":"чтобы были независимые личности, чтобы были мнения, отличающиеся от существующих."},"045":{"text":"От основания Османского государства 1300 лет назад и позже"},"046":{"text":"мы видим, что учёные писали на арабском"},"047":{"text":"с первого дня на темы тафсира, хадисов, а также медицины"},"048":{"text":"и астрономии. Все ученые писали свои труды на арабском языке."},"049":{"text":"Вся научная традиция была построена"},"050":{"text":"на арабском языке."},"051":{"text":"Не зная хорошо арабский язык ты не мог обучиться ни одной науке."},"052":{"text":"Почти все слова османского языка, особенно научная терминология"},"053":{"text":"были из арабского языка, или производными от него."},"054":{"text":"Конечно, это было из-за богатства арабского языка."},"055":{"text":"Библиотеки и научные манускрипты"},"056":{"text":"все были на арабском языке."},"057":{"text":"Салим I, когда вернулся из путешествия в Каир"},"058":{"text":"и привез священные обязательства,"},"059":{"text":"превратил Халифат в Османское государство,"},"060":{"text":"собрался попросить исламского ученого,"},"061":{"text":"чтобы он сделал арабский язык официальным в Османском государстве,"},"062":{"text":"но к сожалению, шейх уль-ислам (не нашел)"},"063":{"text":"доводов и аргументов для этого."},"064":{"text":"Связь османов"},"065":{"text":"с арабским языком запутанная,"},"066":{"text":"не такая как связь с исламом. Связь с исламом ясная."},"067":{"text":"???"},"068":{"text":"Эта святость арабского языка в его тесной связи с Кораном"},"069":{"text":"и они полагали, что назначение языка в службе Корану."},"070":{"text":"Османское правление,"},"071":{"text":"которое длилось более четырёх веков,"},"072":{"text":"столкнулось с арабским языком"},"073":{"text":"как с большим вызовом программе систематического отуречивания,"},"074":{"text":"которую власти"},"075":{"text":"поддерживали или практиковали."},"076":{"text":"Нельзя говорить, что османы продолжали"},"077":{"text":"поддерживать арабский, в то время как они приняли указ об отуречивании."},"078":{"text":"И это отуречивание делалось во всём турецком мире, и арабы входили в него."},"079":{"text":"Они отуречивали не только язык, но и человеческие ресурсы."},"080":{"text":"Мухаммад Али Аль-Албаний, который предпринял возрождение Египта,"},"081":{"text":"человек, специализировавшийся на быстром возрождении."},"082":{"text":"Это египетское чудо связано с ним."},"083":{"text":"Был он албанец или османец (не важно), его привели османы."},"084":{"text":"Он правил Египтом"},"085":{"text":"40 лет не зная ни одной арабской буквы."},"086":{"text":"При этом хорошо да!"},"087":{"text":"С сердцем, полным веры да!"},"088":{"text":"С пренебрежением к ним да. Турки начали"},"089":{"text":"сокращать арабский язык до нескольких научных дисциплин."},"090":{"text":"Когда Наполеон вторгся в Египет,"},"091":{"text":"это прибавило трудностей арабскому языку:"},"092":{"text":"обострилась болезнь,"},"093":{"text":"пересохли перья"},"094":{"text":"и затерялся язык во мраке невежества."},"095":{"text":"Погасли умы,"},"096":{"text":"наступил закат."},"000":{"start":0,"end":0,"text":""}}}}}}